
<template>
    <div id="wrapper">
        <v-head></v-head>
        <div class="selectBoard">
            <div class="firstBar">
            学年 <el-select v-model="semester" class="selectBar" clearable placeholder="请选择学年">
                    <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            学院 <el-select v-model="academy" class="selectBar" @change="queryClass" clearable placeholder="请选择学院">
                     <el-option label="机械与汽车工程学院" value="机械与汽车工程学院"></el-option>
                         <el-option label="建筑学院" value="建筑学院"></el-option>
                         <el-option label="土木与交通学院" value="土木与交通学院"></el-option>
                         <el-option label="电子与信息学院" value="电子与信息学院"></el-option>
                         <el-option label="材料科学与工程学院" value="材料科学与工程学院"></el-option>
                         <el-option label="化学与化工学院" value="化学与化工学院"></el-option>
                         <el-option label="轻工科学与工程学院" value="轻工科学与工程学院"></el-option>
                         <el-option label="食品科学与工程学院" value="食品科学与工程学院"></el-option>
                         <el-option label="数学学院" value="数学学院"></el-option>
                         <el-option label="物理与光电学院" value="物理与光电学院"></el-option>
                         <el-option label="经济与贸易学院" value="经济与贸易学院"></el-option>
                         <el-option label="建筑自动化科学与工程学院" value="建筑自动化科学与工程学院"></el-option>
                         <el-option label="计算机科学与工程学院" value="计算机科学与工程学院"></el-option>
                         <el-option label="电力学院" value="电力学院"></el-option>
                         <el-option label="生物科学与工程学院" value="生物科学与工程学院"></el-option>
                         <el-option label="环境与能源学院" value="环境与能源学院"></el-option>
                         <el-option label="软件学院" value="软件学院"></el-option>
                         <el-option label="工商管理学院" value="工商管理学院"></el-option>
                         <el-option label="公共管理学院" value="公共管理学院"></el-option>
                         <el-option label="马克思主义学院" value="马克思主义学院"></el-option>
                         <el-option label="外国语学院" value="外国语学院"></el-option>
                         <el-option label="法学院" value="法学院"></el-option>
                         <el-option label="新闻与传播学院" value="新闻与传播学院"></el-option>
                         <el-option label="艺术学院" value="艺术学院"></el-option>
                         <el-option label="体育学院" value="体育学院"></el-option>
                         <el-option label="设计学院" value="设计学院"></el-option>
                         <el-option label="医学院" value="医学院"></el-option>
                         <el-option label="国际教育学院" value="国际教育学院"></el-option>
               
                </el-select>
            所在班级 <el-select v-model="inClass" class="selectBar" clearable placeholder="请选择班级">
                    <el-option v-for="item in options2" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                  </el-select>
            <el-button type="primary" @click="searchStudent" id="query">查询</el-button>
            </div>
            <div class="clear"></div>
        </div>
        
              <div class="tableBar">
                 <div id="table">
                <el-table
                :data="tableData"
                id="stuTable"
                border
                :default-sort = "{prop: 'year',order: 'descending'}"
                >

                <el-table-column  
                  prop="year"
                  label="学年"
                  min-width="10%"
                  sortable>
                </el-table-column>
                <el-table-column
                  prop="college"
                  label="学院"
                min-width="20%">
                </el-table-column>
                <el-table-column
                  prop="class"
                  label="班级"
                  min-width="20%">
                </el-table-column>
                <el-table-column
                  prop="stuId"
                  label="学号"
                  min-width="20%"
                  sortable>
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="姓名"
                  min-width="20%">
                </el-table-column>
                <el-table-column
                  label="操作"
                   width="100">
                <template slot-scope="scope">
                <el-button @click="viewInfo(scope.row)" type="text" size="small">查看</el-button>
                <el-button @click="modifyInfo(scope.row)" type="text" size="small">修改</el-button>
                <el-button @click="deleteInfo(scope.row)" type="text" size="small">删除</el-button>
                </template>
                </el-table-column>
              </el-table>
			  <br/>
            <el-button type="primary" @click="add()">添加</el-button>
                </div>
             </div>

   
        
    </div>
</template>
<script>
import vHead from '../commons/AdminstratorNav.vue';
export default {
  components:{
      vHead,
  },
  data(){
      return{
	  
	     options1:[{
		  //value:'88',
		  //label:'555'
		  }],
		  options2:[],
          semester:'',
          academy:'',
          inClass:'',
          tableData:[{
              year:'2015',
              college:'软件学院',
              class:'软件二班',
              stuId:'201530612330',
              name:'林梓龙',
              
          },
          {
              year:'2016',
              college:'软件学院',
              class:'软件三班',
              stuId:'201630113231',
              name:'刘亦菲',
             
          }
          ]
      }
  },
  methods:{
    queryClass(){
        if(this.academy==''){
            this.$message({
                type:'info',
                message:'学院不能为空'
            })
        }else if(this.semester==''){
            this.$message({
                type:'info',
                message:'学年不能为空'
            })
        }else{

        console.log(this.academy)
        var vm=this;
        $.ajax({
            type:'post',
            dataType:'json',
            url:'administor/queryClass',
            data:{
                "year":vm.semester,
                "department":vm.academy
            },
            success:function(response){
                if(response.string){

                }
            },
            error:function(err){
                console.log(err);
                console.log("error");
            }
        })
        }
    },
    analysisClass(vm,classList){
        var clazz=classList.split(';');
        console.log(clazz.length);
        for(var i=0;i<clazz.length;i++){
            var dist=new Array();
            var ele=clazz[i].split(',');
            for(var j=0;j<ele.length;j++){
                var eles=ele[j].split(':');
                console.log(eles[0],'>',eles[1]);
                dist[eles[0]]=eles[1]
            }
             vm.options2.push(dist);
        }
        console.log(vm.options2)
    },

    loadGrade(){
	var myDate=new Date();
	var k=myDate.getFullYear();
	k=k-4;
	while(k!=2018){
	var m={value:k,label:k,}
	this.options1.push(m);
	k=k+1;
	//console.log(k);
	}
	},
	searchStudent(){

        //  var vm=this;
        //  $.ajax({
        //      type:'post',
        //      dataType:'json',
        //      url:'',
        //      data:{
        //         "claId":
        //      },
        //      success:function(response){
                
        //     },
        //     error:function(err){
                
        //     }

        // })

	},
    viewInfo(row){
        this.$router.push({path: '/adminstrator/studentInfo/'+row.stuId})
    },
     modifyInfo(row){
        this.$router.push({path: '/adminstrator/studentInfoModify/'+row.stuId})
    },
     deleteInfo(row){
         
    },
    add(){
        this.$router.push({path: '/adminstrator/studentInfoAdd/'})
    }

  },
   mounted(){
	this.loadGrade();
	}
}
</script>
<style scoped>
#info{
    width:96%;
    margin:0px auto;
    padding-top:10px;
}
#infoTable{
    border-color:#eefff6;
   
}
#query{

    width:100px;
}
.selectBoard{
    margin-top: 10px;
    background-color: #eef1f6;
    width:100%;
    height:50px;
}
.firstBar{
   
    margin-left:5px;
}
.firstBar .selectBar{
    margin:5px 20px 5px 0px;
    
}
.scoreBoard{
    margin-top: 10px;
    background-color: #eef1f6;
    width:100%;
    height:800px;
}
.lbl{
    font-family: "微软雅黑";
    color:#CDC1C5;
}
#table{
     margin:0px auto;
    margin-top:20px;
    width:95%;
}
</style>



